import { Table } from "react-bootstrap";
import { Aspect } from "./Aspect";

export function Task({ task }) {
  return (
    <>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th className="text-center">Szempontok megnevezse</th>
            <th className="text-center">Elrt pontszm / Maximlis pontszm</th>
            <th className="text-center">Szempont lersa</th>
          </tr>
        </thead>
        <tbody>
          {task["aspects"].map((aspect) => (
            <Aspect
              key={aspect["id"]}
              aspect={aspect}
              taskName={task["name"]}
            />
          ))}
        </tbody>
      </Table>
    </>
  );
}
